import React, { Component } from 'react';
import { Pop, Button } from 'zent';

class ZentPopDemo5 extends Component {
  confirmHandler = () => {
    Sweetalert.alert({
      content: 'Pop',
      parentComponent: this
    });
  }

  render() {
    return (
      <Pop
        trigger="click"
        content=""
        type="danger"
        confirmText="Error"
        cancelText="Close"
        onConfirm={this.confirmHandler}
      >
        <Button type="primary"></Button>
      </Pop>
    );
  }
}

export default ZentPopDemo5;
