import React, { Component } from 'react';
import { Sweetalert, Button } from 'zent';

class SweetAlertDemo1 extends Component {
  showAlertInfo = () => {
    Sweetalert.alert({
      content: '',
      parentComponent: this
    });
  }

  foo = () => {
    Sweetalert.alert({
      content: (
        <div>
          <Button onClick={this.foo}>mmm</Button>
        </div>
      ),
      parentComponent: this
    });
  }

  render() {
    return (
      <>
        <Button onClick={this.showAlertInfo}></Button>
        <Button onClick={this.foo}></Button>
      </>
    );
  }
}

export default SweetAlertDemo1;
