import React, { Component } from 'react';
import { Notify, Button } from 'zent';

class NotifyZentDemo2 extends Component {
  render() {
    return (
      <div>
        <Button onClick={() => Notify.config({ duration: 1000 })}>1s</Button>
        <Button onClick={() => Notify.config({ duration: 2000 })}>2s</Button>
        <Button onClick={() => Notify.config({ duration: 3000 })}>3s</Button>
        <br />
        <br />
        <Button onClick={() => Notify.success('')}></Button>
        <Button onClick={() => Notify.error('')}></Button>
      </div>
    )
  }
}

export default NotifyZentDemo2;
