import React, { Component } from "react";
import { Input, Button } from 'zent';

class ZentInputDemo7 extends Component {
  focusAndSelectAll = () => {
    this.input.focus();
    this.input.select();
  }

  selectTheThirdChar = () => {
    this.input.focus();
    this.input.select(2, 3);
  }

  render() {
    return (
      <div>
        <Input defaultValue='' ref={input => this.input = input} />
        <Button onClick={this.focusAndSelectAll}></Button>
        <Button onClick={this.selectTheThirdChar}></Button>
      </div>
    );
  }
}

export default ZentInputDemo7;
