import React, { Component } from "react";
import Input from '@ybq/ui/dist/input';
import Button from '@ybq/ui/dist/button';
import "@ybq/ui/dist/input/index.css";

class ShiyeInputDemo7 extends Component {
  focusAndSelectAll = () => {
    this.input.focus();
    this.input.select();
  }

  selectTheThirdChar = () => {
    this.input.focus();
    this.input.select(2, 3);
  }

  render() {
    return (
      <div>
        <Input defaultValue='' ref={input => this.input = input} />
        <Button onClick={this.focusAndSelectAll}></Button>
        <Button onClick={this.selectTheThirdChar}></Button>
      </div>
    );
  }
}

export default ShiyeInputDemo7;
